export default {
	data() {
		return {
			modalName: ''
		}
	},
	methods: {
		showModal(e) {
			let that = this
			that.modalName = e.currentTarget.dataset.target
		},
		hideModal() {
			let that = this
			that.modalName = null
		}
	}
}
